<template>
<div id="personalData">
  <el-col :span="14" style="margin:30px 0px 0px 0px;">
    <el-row>
      <el-col :span="6" :offset="1">
        <div id="headPortrait"><img :src="headPortrait" style="height:200px;"></img></div>
      </el-col>
      <el-col :span="14" :offset="2">
        <el-row style="margin:10px 0px 20px 0px;">
          <el-col :span="16">
            <el-row>
              <el-col :span="4">:</el-col>
              <el-col :span="16">{{ name }}</el-col>          
            </el-row>
            <el-row>
              <el-col :span="4">:</el-col>
              <el-col :span="20">{{ gender }}</el-col>
            </el-row>
            <el-row>
              <el-col :span="4">:</el-col>
              <el-col :span="20">{{ college }}</el-col>
            </el-row>
            <el-row>
              <el-col :span="4">:</el-col>
              <el-col :span="6">{{ follows }}</el-col>
              <el-col :span="4">:</el-col>
              <el-col :span="6">{{ fans }}</el-col>
            </el-row>
          </el-col>
          <el-col :span="8">
            <el-button type="button"><i class="el-icon-edit"></i>
              
            </el-button>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">:</el-col>
        </el-row>
        <el-row>
          <el-col :span="24">{{ personalIntro }}</el-col>
        </el-row>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="22" :offset="1" style="background-color:green;">
      
      </el-col>
    </el-row>
  </el-col>
  
  <el-col :span="6" style="margin:30px 0px 0px 0px;">
    <el-col id="tableTitle">
      {{ name }}
    </el-col>  
    <el-table :data="tableData" stripe border style="width:100%">
    <el-table-column prop="course" label="" :span="12" >
    </el-table-column>
    <el-table-column prop="points" label="" :span="12">
    </el-table-column>
  </el-table>      
  </el-col>
</div>
</template>

<script>
import hp from './../assets/headportrait.jpg'
export default {
  name: 'personalData',
  data () {
    return {
      name: '',
      gender: '',
      college: '',
      follows: 0,
      fans: 0,
      personalIntro: '~',
      tableData: [
        {
          course: '',
          points: '100'
        },
        {
          course: '',
          points: '38'
        },
        {
          course: '',
          points: '19'
        },
        {
          course: 'C++/C#',
          points: '3'
        }
      ],
      headPortrait: hp
    }
  },
  methods: {
  }
}
</script>

<style scpoed>
  #headPortrait {
    height: 200px;
    font-size: 20px;
    text-align: center;
    line-height: 200px;
  }
  #tableTitle {
    font-weight: bold;
    text-align: center;
    
    height: 40px;
    font-size: 20px;
  }
</style>


